import React from "react"
import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom"
import './App.css';

import Header from "./components/Header"
import Footer from "./components/Footer"
import Home from "./components/Home"
import Create from "./components/Create"
import Login from "./components/Login"
// import Register from "./components/Register"
import Settings from "./components/Settings"
import Article from "./components/Article"
import Profile from "./components/Profile"

import Cookies from "js-cookie"
import Pubsub from "pubsub-js"

class App extends React.Component{

  constructor(props){
    super(props);

    this.state = {
      auth: ''
    }
  }

  componentDidMount(){
    let that = this;
    Pubsub.subscribe("auth",function(msg,data){
      that.setState({
        auth : data
      })
    })
  }

  render() {
    if(Cookies.get("auth")){
      return (
        <Router>
          <Header></Header>
          <Switch>
            <Route exact path="/m" component={Home}></Route>
            <Route path="/editor" component={Create}></Route>
            <Route path="/settings" component={Settings}></Route>
            <Route path="/register" component={Home}></Route>
            <Route path="/login" component={Home}></Route>
            <Route path="/article" component={Article}></Route>
            <Route path="/profile" component={Profile}></Route>
            <Redirect from="/" to="/m"></Redirect>
          </Switch>
          <Footer></Footer>
        </Router>
      )
    }else{
      return (
        <Router>
          <Header></Header>
          <Switch>
            <Route exact path="/m" component={Home}></Route>
            <Route path="/editor" component={Home}></Route>
            <Route path="/settings" component={Home}></Route>
            <Route path="/register" component={Login}></Route>
            <Route path="/login" component={Login}></Route>
            <Route path="/article" component={Home}></Route>
            <Route path="/profile" component={Home}></Route>
            <Redirect from="/" to="/m"></Redirect>
          </Switch>
          <Footer></Footer>
        </Router>
      )
    }
  }
}

export default App;
