import React, { Component } from 'react'
import axios from "axios"
import {addFavorite, deleFavorite} from "../axios/favorite"

export default class Home extends Component {
    constructor(props){
        super(props);
        this.state = {
            data: {
                articles: [],
            }, // 评论列表
            tagsList: [], // 导航列表
            favoritedisabled:false, // 禁止点击
            activeClass: 0, // 高亮显示
            limit: 30, // 每页显示条数
            count:'', // 总页数,
            offset:0, // 当前显示页
        }

        this.changeActive = this.changeActive.bind(this);
        this.fnFavorite = this.fnFavorite.bind(this)
    }

    componentDidMount(){
        axios({
            method:'get',
            url:'/api/tags',
            params:{

            }
        }).then(res => {
            // console.log(res);
            if(res.status === 200){
                this.setState({
                    tagsList : res.data.tags
                })
            }else{
                console.error(res.statusText);
            }
        })

        axios({
            method:'get',
            url:'/api/articles',
            params:{
                limit : this.state.limit,
                offset : this.state.offset
            }
        }).then(res => {
            // console.log(res);
            if(res.status === 200){
                let nums =  res.data.articlesCount / this.state.limit;
                let count =  res.data.articlesCount % this.state.limit === 0 ? nums : nums + 1;
                this.setState({
                    data : res.data,
                    count : parseInt(count)
                })
                // console.log(this.state.count);
            }else{
                console.error(res.statusText);
            }
        })
    }


    changeActive = (id) => {
        // console.log(id)

        axios({
            method:'get',
            url:'/api/articles',
            params:{
                limit : this.state.limit,
                offset : id
            }
        }).then(res => {
            // console.log(res);
            if(res.status === 200){
                this.setState({
                    data : res.data,
                    activeClass : id
                })
            }else{
                console.error(res.statusText);
            }
        })
    }

    async fnFavorite(item) {
        console.log(item);

        // 按钮不能被点击
        await this.setState({
            favoritedisabled : true,
        })
        
        // 判断是否已经被点击
        if(item.favorited){
            console.log("进入取消页面");
            // 取消点赞
            await deleFavorite(item).then(res => {
                console.log("取消",res);
            })
            item.favorited = false;
            item.favoritesCount -= 1;

            // 点击将新的点赞数和状态赋给之前的数据
            let key = this.state.data.articles.indexOf(item);
            this.state.data.articles[key] = item;
            this.setState({
                articles : this.state.data.articles
            })
        }else{
            // 点赞
            await addFavorite(item).then(res => {
                console.log("添加",res);
            })
            item.favorited = true;
            item.favoritesCount += 1;

            // 点击将新的点赞数和状态赋给之前的数据
            // console.log(this.state.data.articles.indexOf(item));
            let key = this.state.data.articles.indexOf(item);
            this.state.data.articles[key] = item;
            this.setState({
                articles : this.state.data.articles
            })
        }

        // 按钮可以再次被点击
        await this.setState({
            favoritedisabled : false,
        })
    }

    render() {
        let count = this.state.count
        return (
            <div className="home-page">

                <div className="banner">
                    <div className="container">
                    <h1 className="logo-font">conduit</h1>
                    <p>A place to share your knowledge.</p>
                    </div>
                </div>

                <div className="container page">
                    <div className="row">

                    <div className="col-md-9">
                        <div className="feed-toggle">
                        <ul className="nav nav-pills outline-active">
                            <li className="nav-item">
                            <a className="nav-link disabled" href="/">Your Feed</a>
                            </li>
                            <li className="nav-item">
                            <a className="nav-link active" href="/">Global Feed</a>
                            </li>
                        </ul>
                        </div>

                        {
                            this.state.data.articles.map((items,index) => {
                                return (
                                    <div className="article-preview" key={index}>
                                        <div className="article-meta">
                                            <a href="profile.html">
                                                <img src={items.author.image} alt="小丑竟是我自己" title="小丑竟是我自己"/>
                                            </a>
                                            <div className="info">
                                            <a href="/" className="author">{items.author.username}</a>
                                            <span className="date">{items.updatedAt}</span>
                                            </div>
                                            <button 
                                                className="btn btn-outline-primary btn-sm pull-xs-right"
                                                onClick={() => {
                                                    this.fnFavorite(items)
                                                }}
                                                disabled={this.state.favoritedisabled}
                                            >
                                            <i className="ion-heart"></i> {items.favoritesCount}
                                            </button>
                                        </div>
                                        <a href="/" className="preview-link">
                                            <h1>{items.title}</h1>
                                            <p>{items.description}</p>
                                            <span>Read more...</span>
                                        </a>
                                        </div>
                                )
                            })
                        }
                        <nav>
                            <ul className="pagination">
                                {
                                    Array.from(Array(count), (e, i) => {
                                        // console.log(e,i)
                                            return <li 
                                                className={this.state.activeClass === i ? 'page-item ng-scope active' : 'page-item ng-scope'}
                                                onClick = {() => {
                                                    this.changeActive(i)
                                                }}
                                                key={i}
                                                >
                                                <span className="page-link ng-binding">{i + 1}</span>
                                        </li>
                                      })
                                }
                                
                            </ul>
                        </nav>
                    </div>

                    <div className="col-md-3">
                        <div className="sidebar">
                        <p>Popular Tags</p>

                        <div className="tag-list">
                            {
                                this.state.tagsList.map((itemTag,index) => {
                                    return <a href="/" className="tag-pill tag-default" key={index}>{itemTag}</a>
                                })
                            }
                        </div>
                        </div>
                    </div>

                    </div>
                </div>

                </div>
        )
    }
}
