import React from "react"
import { NavLink } from "react-router-dom"
import Cookies from "js-cookie"
import Pubsub from "pubsub-js"

import "../../main.css"

class Headers extends React.Component{

  constructor(props){
    super(props);

    this.state = {
      auth : ""
    }
  }

  componentDidMount(){
      let that = this;
      Pubsub.subscribe("auth",function(msg,data){
        // console.log(msg,'msg'); // 获取的是 auth，cookies中的key
        // console.log(data,'data'); // 获取的是 秘钥，cookies中的value

        // 将Cookies中的auth  和  state中的   auth  进行合并
        that.setState({
          auth : data
        })
      })
  }
  render() {
    if(Cookies.get("auth")){
      return (
        <div>
          <nav className="navbar navbar-light">
              <div className="container">
                  <a className="navbar-brand" href="/">conduit</a>
                  <ul className="nav navbar-nav pull-xs-right">
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/m">Home</NavLink>
                  </li>
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/editor">
                      <i className="ion-compose"></i>&nbsp;New Post
                      </NavLink>
                  </li>
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/settings">
                      <i className="ion-gear-a"></i>&nbsp;Settings
                      </NavLink>
                  </li>
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/profile">
                      <img src="https://z3.ax1x.com/2021/04/28/gCzpmq.jpg" style={{width:'26px',height:'26px','display':'inline-block','borderRadius': '50%'}} alt="小丑竟是我自己" title="小丑竟是我自己" />
                      <span>小丑is me</span>
                      </NavLink>
                  </li>
                  </ul>
              </div>
          </nav>
        </div>
      )
    }else{
      return (
        <div>
          <nav className="navbar navbar-light">
              <div className="container">
                  <a className="navbar-brand" href="/">conduit</a>
                  <ul className="nav navbar-nav pull-xs-right">
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/m">Home</NavLink>
                  </li>
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/login">Sign in</NavLink>
                  </li>
                  <li className="nav-item">
                      <NavLink className="nav-link" to="/register">Sign up</NavLink>
                  </li>
                  </ul>
              </div>
          </nav>
        </div>
      )
    }
  }
}

export default Headers;
