import React, { Component } from 'react'
import axios from "axios"
import Cookies from "js-cookie"
import Pubsub from "pubsub-js"

class A extends React.Component {
    blurName(e){
        Pubsub.publish("value",e.target.value)
    }
    render() {
      return (
        <div>
          <fieldset className="form-group">
            <input
              className="form-control form-control-lg"
              type="text"
              onBlur={this.blurName}
              placeholder="Your Name"
            />
          </fieldset>
        </div>
      );
    }
  }

export default class Login extends Component {
    constructor(props){
        super(props);
        
        this.state = {
            username:'',
            password:'',
            email:''
        }

        this.blurEmail = this.blurEmail.bind(this);
        this.blurPassword = this.blurPassword.bind(this);
        this.handlerSub = this.handlerSub.bind(this);
    }

    componentDidMount(){
        // console.log(this.props.match.path,'props');
        let that = this
        Pubsub.subscribe("value",function(msg,data){
            that.setState({
                username:data
            })
        })
    }

    isLogin(){
        return this.props.match.path === "/login"
    }

    blurEmail(e){
        this.setState({
            email : e.target.value
        })
    }
    blurPassword(e){
        this.setState({
            password : e.target.value
        })
    }
    handlerSub(e){
        e.preventDefault();
        console.log("------");
        
        let user = {
            username: this.state.username?this.state.username : "",
            email : this.state.email,
            password : this.state.password
        }
        axios({
            method:'post',
            url:this.state.username ? '/api/users' : '/api/users/login',
            data:{
                user:user
            }
        }).then(res => {
            console.log(res);
            // console.log(res.data.user.token); // 秘钥 存储在cookies中的value值
            Cookies.set("auth",res.data.user.token); // 将数值存储在 cookies中，以键值对的形式存储
            // console.log(this.props);
            // 点击登陆成功后跳转路由
            this.props.history.push("/");
            // 发布
            Pubsub.publish("auth",res.data.user.token);
        })
    }
    render() {
        return (
            <div className="auth-page">
                <div className="container page">
                    <div className="row">
                    <div className="col-md-6 offset-md-3 col-xs-12">
                        <h1 className="text-xs-center">
                            {this.isLogin() ? "Sign in" : "Sign up"}
                        </h1>
                        <p className="text-xs-center">
                        <a href="/">
                            {this.isLogin() ? "Need an account?" : "Have an account?"}
                        </a>
                        </p>

                        <ul className="error-messages">
                        <li>That email is already taken</li>
                        </ul>

                        <form 
                            >
                        {this.isLogin() ? '' : <A></A>}
                        <fieldset className="form-group">
                            <input 
                                className="form-control form-control-lg" 
                                type="text" 
                                onBlur={this.blurEmail}
                                placeholder="Email"
                            />
                        </fieldset>
                        <fieldset className="form-group">
                            <input 
                                className="form-control form-control-lg" 
                                type="password" 
                                onBlur={this.blurPassword}
                                placeholder="Password"
                            />
                        </fieldset>
                        <button className="btn btn-lg btn-primary pull-xs-right" onClick={this.handlerSub}>
                            {this.isLogin() ? "Sign in" : "Sign up"}
                        </button>
                        </form>
                    </div>

                    </div>
                </div>
                </div>
        )
    }
}
